.profile {
  background: linear-gradient(
    to right,
    #a1bfe5 0%,
    pink 50%,
    #f0f1f0 50%,
    #e7cab5 100%
  );
  height: 100vh;
  display: flex;
}

.submission {
  width: 50vw;
  /* position: absolute; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* top: 15%; */
  /* left: 24%; */
  /* transform: translateX(-15%, -24%); */
}

.report {
  width: 50vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile_img {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  cursor: pointer;
}

.report_data {
  font-family: "IM Fell Great Primer", serif;
  color: #7d1a6c;
  margin: 2% 0;
}
